<template>
<div class="question">
  <div class="head">
    <h3>正在对{{leader}}进行评价</h3>
  </div>
  <div class="questionArea">
    <div v-for="(item, index) in qArr" class="text item">
      <p>{{index+1}}.{{ item.question }}</p>
      <div class="block" style="paddingLeft:15px;">
        <el-rate
          v-model="valueArr[index]"
          :colors="['#666', '#F7BA2A', '#FF9900']"
          show-text
          @change="getRate">
        </el-rate>
      </div>
    </div>
    <hr>
    <div class="btncenter">
      <el-button type="primary" round @click="btnSubmit">完成</el-button>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'question',
  data() {
    return {
      uid: '',
      leader: '',
      qArr:[],
      valueArr:[],
      scoreArr:[]
    }
  },
  created () {
    var that=this;
    this.uid=this.$route.params.uid;
    this.leader=this.$route.params.leader;
    console.log(this.uid,this.leader)
    this.$axios.get('.././question/static/data/question.php')
      .then(function(res) {
        that.qArr=res.data
        console.log(that.qArr)
      })
      .catch(function(err) {
        console.log(err);
      })
  },
  methods: {
    getRate (value) {
      console.log(value);
      console.log(this.valueArr)
    },
    btnSubmit () {
      var that=this
      // 验证数组是否每项都有值
      for(var i=0;i<that.valueArr.length;i++){
          that.scoreArr[i]=that.valueArr[i]
          //第2,3,4,8,10,12题分值乘以2
          if(i===1||i===2||i===3||i===7||i===9||i===11){
            that.scoreArr[i]=that.valueArr[i]*2
          }
          if(that.valueArr[i]===0){
            that.$message({
              message: "请检查将每项填写完整！",
              type: 'error',
              center: true,
            })
            return
          }
      }
    console.log(that.scoreArr)
    //发送ajax请求
    that.$axios.get('.././question/static/data/insertquest.php', {
        params: {
          uid: that.uid,
          leader: that.leader,
          scoreArr: that.scoreArr
        }
      })
      .then(function(res) {
        console.log(res.data.code)
        if (res.data.code == "1") {
          that.$router.push('/testpage/'+that.uid)
        } else {
          that.$message({
            message: "评价提交失败！",
            type: 'warning',
            center: true
          })
        }
      })
      .catch(function(err) {
        console.log(err);
      })



    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.question{
  background-color: #FCFAF0;
}
.head{
  position:fixed;
  top:0;
  background-color: #F9CB94;
  z-index: 999;
  width:100%;
  text-align:center;
}
.questionArea{
  /* border:1px solid #ddd; */
  padding-top:50px;
  width:75%;
  margin:0 auto;
}
.text {
   font-size: 14px;
 }
 .item {
   padding: 10px 0;
 }
 .box-card {
   width: 480px;
 }
 .btncenter{
   text-align: center;
   padding-bottom:10px;
 }
</style>



