<template>
<div class="hello">
  <el-dialog
    title="提示"
    :visible.sync="dialogVisible"
    width="90%"
    :before-close="handleClose"
    center
    >
    <p class="testLeft">&nbsp;&nbsp;&nbsp;&nbsp;亲爱的家人非常感谢您在百忙之中抽出时间填写我们的调查问卷。在过去一年的工作中，我们希望看到您对公司的未来发展、对上级的满意程度和您个人打算的真实想法，以便为公司为提高管理水平、更好适应未来发展等提供依据。谢谢合作！您反馈的结果汇总后公司会认真如实地进行讨论和参考。</p>
    <span>欢迎参加本次答题，本次为匿名答题，请认真作答，确保答案真实性。1-5分，1分为最低，5分为最高。</span>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="dialogVisible = false">知道了</el-button>
    </span>
  </el-dialog>
  <div class="login">
    <h1 class="center">满意度调查</h1>
    <div class="loginBody">
      <el-form :model="form">
        <el-form-item>
          <el-input v-model="form.utel" auto-complete="off" prefix-icon="el-icon-mobile-phone" placeholder="请输入手机号" @blur="utelBlur"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="form.pic" auto-complete="off" prefix-icon="el-icon-picture" placeholder="请输图片验证码" style="width:65%;"></el-input>
          <img :src="picCodeSrc" style="verticalAlign:middle;" @click="changePicCode" class="checkImg">
        </el-form-item>
        <el-form-item>
          <el-input v-model="form.msg" auto-complete="off" prefix-icon="el-icon-message" placeholder="请输短信验证码" style="width:70%;"></el-input>
          <el-button @click="getMsgCode" type="primary" :disabled="btnCanClick">{{btnHtml}}</el-button>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer center">
        <el-button type="primary" @click="voteSubmit">确 定</el-button>
      </div>
      </el-form>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      dialogVisible: true,
      btnHtml: '获取',
      btnCanClick: false,
      valiutel: false,
      valiPicCode: false,
      valiMsgCode: false,
      picCodeSrc: '.././question/static/checkcode.php',
      form: {
        utel: '',
        pic: '',
        msg: ''
      }
    }
  },
  methods: {
    handleClose(done) {
        done();
      },
    voteSubmit() {
      var that = this
      this.$axios.get('.././question/static/data/valiMsgCode.php', {
          params: {
            msg: this.form.msg,
            utel: this.form.utel
          }
        })
        .then(function(res) {
          if (res.data.code == "1") {
            that.valiMsgCode = true
            if (that.valiutel && that.valiPicCode && that.valiMsgCode) {
              var uid=res.data.uid
              that.$message({
                message: "验证成功,3秒后跳转",
                type: 'success',
                center: true
              })
              var t = setTimeout(function() {
                that.$router.push('/testpage/'+uid)
              }, 3000)
            } else {
              that.$message({
                message: "请检查信息是否正确",
                type: 'warning',
                center: true
              })
            }

          } else {
            that.valiMsgCode = false
            that.$message({
              message: "短信验证码错误",
              type: 'warning',
              center: true
            })
          }
        })
        .catch(function(err) {
          console.log(err);
        })
    },
    utelBlur() {
      var that = this
      var regtel = /^[1][3,4,5,7,8,9][0-9]{9}$/i
      this.valiutel = regtel.test(this.form.utel)
      if (this.valiutel === false) {
        this.$message({
          message: '请检查电话号码格式！',
          type: 'warning',
          center: true
        })
      } else {
        that.$axios.get('.././question/static/data/valiutel.php',{
          params: {
            utel: that.form.utel
          }
        })
        .then(function(res) {
          if(res.data.code == '1'){
            that.valiutel=true
            that.$message({
              message: "电话号码可用！",
              type: 'success',
              center: true
            })
          }else{
            that.valiutel=false
            that.$message({
              message: "此号码无权限！如有疑问请联系人力。",
              type: 'warning',
              center: true
            })
          }
          console.log(res);
        })
        .catch(function(err) {
          console.log(err);
        })
      }
    },
    getMsgCode() {
      var that = this
      this.form.pic = this.form.pic.toLowerCase().replace(/\s+/g, "")
      if (this.valiutel) {
        this.$axios.get('.././question/static/data/picCode.php', {
            params: {
              pic: this.form.pic
            }
          })
          .then(function(res) {
            if (res.data.code == "1") {
              that.valiPicCode = true
              if (that.valiutel && that.valiPicCode) {
                that.$axios.get('.././question/static/data/api_demo/SmsDemo.php', {
                    params: {
                      utel: that.form.utel
                    }
                  })
                  .then(function(res) {
                    console.log(res)
                    var index = res.data.indexOf("[Code]")
                    var msgCode = res.data.substring(index + 10, index + 12)
                    console.log(msgCode)
                    if (msgCode == "OK") {
                      that.btnHtml = 60
                      that.btnCanClick = true
                      var timer = setInterval(function() {
                        that.btnHtml--
                          if (that.btnHtml <= 0) {
                            clearInterval(timer)
                            that.btnHtml = '获取'
                            that.btnCanClick = false
                          }

                      }, 1000)
                      that.$message({
                        message: "短信验证码已发送",
                        type: 'success',
                        center: true
                      })
                    } else {
                      that.$message({
                        message: "短信验证码发送失败",
                        type: 'warning',
                        center: true
                      })
                    }
                  })
                  .catch(function(err) {
                    console.log(err)
                  })
              }

            } else {
              that.valiPicCode = false
              that.$message({
                message: "图片验证码错误",
                type: 'warning',
                center: true
              })
            }
          })
          .catch(function(err) {
            console.log(err)
          })

      } else {
        that.$message({
          message: "该电话号码不可用，请检查格式，或是否有权限",
          type: 'warning',
          center: true
        })
      }
    },
    changePicCode() {
      this.picCodeSrc = ".././question/static/checkcode.php?" + Math.random()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 80%;
  height: 300px;
  border: 1px solid #fff;
  border-radius: 50px;
  padding: 20px 15px;
  background-color: #CEE4E2;
  text-align: left;
}

.checkImg {
  height: 38px;
}

.center {
  text-align: center;
}
</style>



