<template>
<div class="testpage">
  <div class="mainarea">
    <p>{{uname}}你好，你要将对以下{{leaderNum}}位领导做出评价。</p>
    <el-row>
      <el-col :span="22" :offset="1" v-for="(item,index) in leaderArr" class="mycard">
        <div @click="jumpTo(item.leader,item.submited)">
          <el-row>
            <el-col :span="10">
              <img src="../assets/person.png" alt="">
            </el-col>
            <el-col :span="14">
              <div v-if="item.submited" class="states"></div>
              <span>姓名：</span>
              <h3 style="text-align:center;">{{item.leader}}</h3>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
  </div>
</div>
</template>

<script>
export default {
  name: 'Note',
  created () {
    var that=this
    this.uid=this.$route.params.uid;
    this.$axios.get('.././question/static/data/leader.php',{
      params: {
        uid: that.uid
      }
    })
    .then(function(res) {
      console.log(res.data);
      var arr=res.data;
      that.uname=arr[0].uname
      that.leaderNum=arr[0].leaderNum
      for(var i=1;i<arr.length;i++){
        if(arr[i].submited=="1"){
          arr[i].submited=true
        }else{
          arr[i].submited=false
        }
        that.leaderArr[i-1]=arr[i]
      }
      console.log(that.leaderArr)
    })
    .catch(function(err) {
      console.log(err);
    })
  },
  data() {
    return {
      isEvaluated: true,
      uid: '',
      uname: '',
      leaderNum: '',
      leaderArr: []
    }
  },
  methods: {
    jumpTo (leader,submited) {
      if(submited){
        this.$message({
          message: "已经评价过了哦！",
          type: 'warning',
          center: true
        })
      }else{
        this.$router.push('/question/'+this.uid+"/"+leader)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.testLeft{
  text-align: left;
}
.mainarea{
  text-align: center;
}
.mycard{
  border:1px solid #ddd;
  border-radius: 20px;
  margin-bottom:5px;
  background-color: #fff;
  text-align: left;
  padding:15px 15px;
}
.states{
  width:50px;
  height:50px;
  position:absolute;
  top:-22px;
  right:-22px;
  background-image: url("../assets/check.png")
}
</style>



